h1 {
    font-family: cursive;
  }
h2 {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
body {
  background: linear-gradient(45deg, #6b6b6b, #ffffff);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0;
  height: 100vh;
  justify-content: center;
  font-family: Arial, sans-serif;
}

.display-ingredients-area ul {
  list-style-type: none;
  padding: 0;
  font-size: large;
  font-style: bold;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.display-instructions-area ul {
  list-style-type: none;
  font-size: large;
  font-style: bold;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.container {
  position: relative;
  width: 300px;
  height: 100px;
}

.text {
  position: absolute;
  opacity: 0;
  left: 0;
  transition: opacity 1s ease, transform 1s ease;
}

.text.move-right {
  opacity: 1;
  transform: translateX(50px);
}

.text2 {
  position: absolute;
  opacity: 0;
  left: 60px; /* 初期位置を右にオフセット */
  transition: opacity 1s ease 1s; /* 遅延を設定 */
}
